.navbar-brand svg,
.navbar-brand img {
  max-width: calc(100vw - 6rem);
  width: 12rem;
  height: 3.5rem;
  transition: width 0.25s, height 0.25s;
}
.navbar-toggler {
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
  color: inherit;
  border: 0 !important;
  box-shadow: none !important;
  z-index: 1046;
  transition: background 0.25s;
}
.navbar-toggler:before,
.navbar-toggler span, .navbar-toggler:after {
  content: "";
  position: absolute;
  top: 50%;
  width: 1.5rem;
  height: 3px;
  display: block;
  background: currentColor;
  border-radius: 0.5rem;
  transition: all 0.25s ease-in-out;
  transform: translate(0, -50%);
}
.navbar-toggler:before {
  margin-top: -0.5rem;
}
.navbar-toggler:after {
  margin-top: 0.5rem;
}
.navbar .offcanvas, .navbar .offcanvas-backdrop {
  top: var(--header-height);
  height: calc(100vh - var(--header-height));
}
.navbar .offcanvas.show ~ .navbar-toggler:before {
  margin-top: 0;
  transform: translate(0, -50%) rotate(45deg);
  transform-origin: center;
}
.navbar .offcanvas.show ~ .navbar-toggler span {
  transform-origin: center;
  transform: translate(0, -50%) scaleX(0);
}
.navbar .offcanvas.show ~ .navbar-toggler:after {
  margin-top: 0;
  transform: translate(0, -50%) rotate(-45deg);
  transform-origin: center;
}
